import React from 'react';
import './footer.styles.css';

// import TestinomialImage from '../../images/person-1.jpg';

const Footer = () => (
<div id="footer">
    <div className='footer-container'>
        <div className='top'>
            <div className='footer-brand'>
                <h1>Educate <span> <br/> Online </span></h1>
                <p>
                    <b>EduOnline </b>is a platform for Learning and education purpose for the students.
                    Enroll today and get your desired course now. EduOnline has a Real Time Learning Management System.
                    It has all the latest feature, which a modern LMS should accomplish.
                </p>
            </div>
            <div className='footer-links'>
                <h1>Important Links</h1>
                <div className='links'>
                <p>Courses</p>
                <p>Services</p>
                <p>Contact</p>
                <p>Login</p>
                <p>Lms</p>
                <p>Admin</p>
                </div>
            </div>
            <div className='footer-contact'>
                <h1>Contact</h1>
                <input type='text' placeholder='Username...'></input>
                <input type='text' placeholder='Email...'></input>
                <button className='footer-contact-btn'>Send</button>
            </div>
        </div>
        <div className='bottom'>
            <h1>Made by Zaki ul Hassan. | All Copyrights are reserved.</h1>
        </div>
    </div>
</div>
);

export default Footer;