import React from 'react';
import './homepage.styles.css';

import Banner from '../Components/banner/banner.component';
import Header from '../Components/header/header.component';
import Services from '../Components/services/services.component';
import FCourses from '../Components/featured-courses/featured-courses.component';
import NewsEvents from '../Components/news-events/news-events.component';
import Testinomial from '../Components/testinomials/testinomial.components';
import Footer from '../Components/footer/footer.component';

function HomePage() {
    return (
      <div>
        <div className='homepage-container'>
          <Header className='header'/>
          <Banner className='banner'/>
          <FCourses className='f-courses'/>
          <Services className='services'/>
          <NewsEvents className='news-events'/>
          <Testinomial className='testinomials'/>
          <Footer className='footer'/>
        </div>
      </div>
    );
  }

export default HomePage;