import React from 'react';
import './banner.styles.css';

import { Link } from "react-scroll";
import bannerImage from '../../images/banner-1.png';

const Banner = () => (
    <div className='banner' id="banner">
        <div className='banner-container'>
           <div className='description'>
                <h1>Educate <span> <br/> Online </span></h1>
                <p>
                    <b>EduOnline </b>is a platform for Learning and education purpose for the students.
                    Enroll today and get your desired course now. EduOnline has a Real Time Learning Management System.
                    It has all the latest feature, which a modern LMS should accomplish.
                </p>
                <br/>
                <button className='b1'>
                <Link
                    className='b1-1'
                    to="courses"
                    spy={true}
                    smooth={true}
                    offset={-40}
                    duration= {500}
                    >Enroll Now
                </Link>
                </button>
                <button className='b2'>
                <Link
                    className='b2-2'
                    to="features"
                    spy={true}
                    smooth={true}
                    offset={-40}
                    duration= {500}
                    >Learn More
                </Link>
                </button>
           </div>
           <div className='image'>
                <img src={bannerImage} alt='banner'/>
           </div>
        </div>
    </div>
);

export default Banner;