import React from 'react';
import './testinomial.styles.css';

import TestinomialImage from '../../images/person-1.jpg';

const Testinomial = () => (
<div>
    <div className='testinomial-container'>
        <div className='testinomial'>
            <div className='test-person'>
                <div className='test-image'>
                    <img src={TestinomialImage} alt='testinomial'/>
                </div>
                <div className='test-name'></div>
            </div>
            <div className='test-description'>
                <p>This was a very detailed course with a lot of information. Hopefully I am truly ready to build websites using Angular, and I believe I am.

                Thanks,</p>
            </div>
        </div>
    </div>
</div>
);

export default Testinomial;